.my-learn-details {
  padding-top: 60px;
  width: 100%;
  background: #F6F7FB;
  padding-bottom: 60px;
  min-width: 1366px;
  /*新增课程公告*/
}
.my-learn-details .ant-card {
  border-radius: 5px;
  padding-bottom: 20px;
}
.my-learn-details .ant-card .ant-card-body {
  padding: 0;
}
.my-learn-details .center-top {
  background: #F6F7FB;
  border: none;
}
.my-learn-details .center-top .center-title {
  margin: 30px 0 26px 0;
}
.my-learn-details .center-top .center-title .center-title-img {
  margin-right: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 134px;
  height: 86px;
  background: url(../../../../assets/images/placeholder_bg.png) no-repeat;
  background-size: 100% 100%;
}
.my-learn-details .center-top .center-title .center-title-img img {
  width: 100%;
  height: 100%;
}
.my-learn-details .center-top .center-title .center-title-img > span {
  padding: 2px 6px;
  position: absolute;
  left: 10px;
  bottom: 10px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 2px;
  color: #fff;
  font-size: 12px;
}
.my-learn-details .center-top .center-title .center-title-img > span .course-logo-l {
  width: 12px !important;
  height: 12px !important;
  vertical-align: -1px;
  margin-right: 5px;
  display: inline-block;
  min-width: auto;
  min-height: auto;
}
.my-learn-details .center-top .center-title .title1 {
  font-size: 20.6874px;
  line-height: 120.7%;
  color: #2A283D;
  margin-bottom: 10px;
}
.my-learn-details .center-top .center-title .title2 {
  font-size: 15.7618px;
  color: #2A283D;
  opacity: 0.4;
  margin-bottom: 10px;
}
.my-learn-details .center-top .center-title .title2 span {
  margin-right: 20px;
}
.my-learn-details .center-top .center-title .title3 {
  margin-bottom: 10px;
}
.my-learn-details .center-top .center-title .title3 .span1 {
  font-size: 12px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.4;
}
.my-learn-details .center-top .center-title .title3 .span2 {
  font-size: 12px;
  color: #2A283D;
  opacity: 0.4;
  margin-left: 15px;
}
.my-learn-details .center-top .center-title .title3 .Progress-content {
  width: 300px;
  margin-left: 20px;
}
.my-learn-details .center-top .center-title .title3 .Progress-content .ant-progress-inner {
  background: #EAEAEA;
  border-radius: 5.91537px;
}
.my-learn-details .center-top .center-title .title3 .Progress-content .ant-progress-success-bg,
.my-learn-details .center-top .center-title .title3 .Progress-content .ant-progress-bg {
  background: linear-gradient(90deg, #1BDDEC 0%, #07BDF9 100%);
  box-shadow: 0px 4px 8px rgba(37, 136, 191, 0.34);
  border-radius: 5.73466px;
}
.my-learn-details .center-top .center-title .center-btn {
  text-align: right;
  position: absolute;
  right: 25px;
  bottom: 0;
}
.my-learn-details .center-top .center-title .center-btn .ant-btn {
  /*width: 56px;
                    height: 17px;
                    font-size: 14px;*/
  color: #2A283D;
  background: #3BB9FF;
  border-radius: 5px;
}
.my-learn-details .center-top .center-title .center-btn .ant-btn span {
  font-weight: normal;
  font-size: 14px;
  line-height: 120.7%;
  color: #FFFFFF;
}
.my-learn-details .center-top .center-title .center-btn .btn-right {
  display: inline-block;
  margin-left: 15px;
  vertical-align: middle;
}
.my-learn-details .center-top .center-title .center-btn .btn-right img {
  box-sizing: border-box;
  display: inline-block;
  margin: -5px 5px 0;
}
.my-learn-details .center-top .center-title .center-btn .btn-right .right-p {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.99;
  display: inline-block;
}
.my-learn-details .center-content {
  margin: 30px 30px 30px 54px;
}
.my-learn-details .center-content .center-content-left {
  padding-right: 27px;
}
.my-learn-details .center-content .center-content-left .ant-tabs {
  margin: 10px 20px 30px 20px;
}
.my-learn-details .center-content .center-content-left .ant-tabs .ant-tabs-bar {
  margin: 0 0 30px 0;
}
.my-learn-details .center-content .center-content-left .ant-tabs-tab {
  color: #2A283D;
  font-size: 18px;
  opacity: 0.5;
}
.my-learn-details .center-content .center-content-left .ant-tabs-tab-active {
  opacity: 1 !important;
}
.my-learn-details .center-content .center-content-left .tab2 .search-titlea .ant-input {
  height: 35px;
  line-height: 35px;
}
.my-learn-details .center-content .center-content-left .tab2 .search-titlea .ant-input:focus {
  border: 1px solid #e6e6e6;
}
.my-learn-details .center-content .center-content-left .tab3 {
  min-height: 500px;
}
.my-learn-details .center-content .center-content-left .tab3 .center {
  text-indent: 2em;
  color: #2A283D;
  height: 100%;
  line-height: 26px;
}
.my-learn-details .center-content .fixed-center {
  position: fixed;
  right: 26px;
}
.my-learn-details .center-content .fixed-center .no-content {
  min-height: 400px;
}
.my-learn-details .center-content .fixed-center .no-content .ant-empty {
  position: absolute;
  left: 40%;
  top: 30%;
  transform: translate(-30%, -50%);
}
.my-learn-details .center-content .fixed-center .center-content-right {
  margin: 0 18px 18px 8px;
}
.my-learn-details .center-content .fixed-center .center-content-right .center-content-right-title {
  font-size: 22px;
  line-height: 120.7%;
  color: #2A283D;
}
.my-learn-details .center-content .fixed-center .center-content-right .center-content-right-img {
  text-align: right;
}
.my-learn-details .center-content .fixed-center .center-content-right .center-content-right-img span {
  color: #00BBF8 !important;
  font-size: 14px;
  line-height: 120.7%;
}
.my-learn-details .center-content .fixed-center .center-content-right .center-content-right-img span img {
  margin-top: -2px;
  margin-right: 10px;
}
.my-learn-details .center-content .fixed-center .center-content-right-content {
  margin: 32px 0 10px 20px;
  cursor: pointer;
}
.my-learn-details .center-content .fixed-center .center-content-right-content .center-content-right-content-img {
  margin-right: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 94px;
  height: 75px;
  position: relative;
  background: url(../../../../assets/images/placeholder_bg.png) no-repeat;
  background-size: 100% 100%;
}
.my-learn-details .center-content .fixed-center .center-content-right-content .center-content-right-content-img img {
  width: 100%;
  height: 100%;
}
.my-learn-details .center-content .fixed-center .center-content-right-content .center-content-right-content-img > span {
  padding: 2px 6px;
  position: absolute;
  left: 2px;
  bottom: 2px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 2px;
  color: #fff;
  font-size: 12px;
}
.my-learn-details .center-content .fixed-center .center-content-right-content .center-content-right-content-img > span .course-logo-l {
  width: 12px !important;
  height: 12px !important;
  vertical-align: -1px;
  margin-right: 5px;
  display: inline-block;
  min-width: auto;
  min-height: auto;
}
.my-learn-details .center-content .fixed-center .center-content-right-content .right-title1 {
  font-size: 18.0844px;
  line-height: 120.7%;
  color: #2A283D;
  margin-bottom: 7px;
}
.my-learn-details .center-content .fixed-center .center-content-right-content .right-title2 {
  color: #2A283D;
  font-size: 14px;
  opacity: 0.4;
  margin-bottom: 5px;
}
.my-learn-details .center-content .fixed-center .center-content-right-content .right-title2:last-child {
  margin-bottom: 0;
}
.my-learn-details .center-content .fixed-center .center-content-right-content:hover .right-title1 {
  color: #00BBF8;
}
.my-learn-details .announcement .search-title {
  margin-bottom: 18px;
}
.my-learn-details .announcement .ant-card-bordered {
  background: #F9F9F9;
  border-radius: 5px;
  margin-top: 8px;
  border: 1px solid #F9F9F9;
}
.my-learn-details .announcement .ant-card-bordered .ant-card-body {
  margin: 20px 0 20px 34px;
}
.my-learn-details .announcement .tab3-center-img {
  width: 60px;
  height: 55px;
  line-height: 55px;
}
.my-learn-details .announcement p {
  margin-bottom: 0;
  height: 30px;
  line-height: 30px;
}
.my-learn-details .announcement .title1 span:first-child {
  width: 90%;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.my-learn-details .announcement .title1 span {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.5;
}
.my-learn-details .announcement .title1 .ant-btn {
  float: right;
  font-size: 12px;
  text-align: center;
  color: #2A283D;
  height: 28px;
  line-height: 28px;
}
.my-learn-details .announcement .title1 .ant-btn span {
  font-size: 12px;
  opacity: 1;
  font-weight: 600;
}
.my-learn-details .announcement .title1-active .ant-btn span {
  color: #2A283D;
  opacity: 0.3;
}
.my-learn-details .announcement .title1-on .ant-btn span {
  color: #FF9142;
  opacity: 0.99;
}
.my-learn-details .announcement .title2 span {
  font-size: 12px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.5;
  margin-bottom: 0;
}
.my-learn-details .announcement .title2 .ant-btn {
  float: right;
  background: #3BB9FF;
  border: 0.539754px solid #309DE4;
  box-sizing: border-box;
  border-radius: 3.77828px;
  height: 28px;
  line-height: 28px;
  color: #FFFFFF !important;
}
.my-learn-details .announcement .title2 .ant-btn .a-link,
.my-learn-details .announcement .title2 .ant-btn span {
  color: #FFFFFF !important;
  opacity: 1;
}
.my-learn-details .announcement .title3 span {
  font-size: 12px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.5;
  margin-bottom: 0;
  padding-right: 20px;
}
.my-learn-details .announcement .title3 .ant-btn {
  float: right;
  background: #3BB9FF;
  border: 0.539754px solid #3BB9FF;
  box-sizing: border-box;
  border-radius: 3.77828px;
  height: 28px;
  line-height: 28px;
  margin-top: -10px;
}
.my-learn-details .announcement .title3 .ant-btn span {
  color: #FFFFFF;
  opacity: 1;
  padding-right: 0;
}
html {
  overflow-x: auto;
